.header-nav {
  font-family: 'Open Sans', sans-serif;

  float: right;

  margin: 7px 0 0 0;

  list-style: none;
  li {
    &:hover >ul {
        display: block;
      }
    .sub-menu {
      position: absolute;
      top: 50px;
      left: 0;

      display: none;

      width: 260px;

      transition: .4s;

      background-color: #3c3c3c;
      background-color: rgba(72,72,72,.9);

      li {
        width: 100%;

        text-align: center;

        border-bottom: 1px solid #80886c;
        a {
          width: 100%;
          padding: 7px 0 7px 0 !important;

          color: #a6ca48;
          background: none !important;
        }
      }
    }
  }
}

.header-nav li:hover .header-nav {
  display: block;
}

.header-nav .header-nav {
  position: absolute;
  top: 50px;
  left: 0;

  display: none;

  width: 260px;

  transition: .4s;

  background-color: rgb(60, 60, 60);
  background-color: rgba(72, 72, 72, .9);
}

.product-availability {
  font-size: 16px;

  float: right;

  width: 100%;

  text-align: center;

  color: #d20e1c;
}

.header-nav .header-nav li:hover {
  background-color: rgb(116, 116, 116);
  background-color: rgba(60, 60, 60, .85);
}

.header-nav .header-nav li {
  width: 100%;

  text-align: center;

  border-bottom: 1px solid #80886c;
}

.header-nav li {
  font-size: 17px;

  position: relative;

  float: left;
}

.blok-header-nav1 .header-nav li.last a {
  padding-right: 0;
}

.header-nav li a {
  font-size: 16px;
  line-height: 26px;

  display: inline-block;

  padding: 11px 8px;

  text-align: center;
  text-decoration: none;

  color: rgba(255, 255, 255, .88);
}

.header-nav li a span.bottom {
  font-size: 16px;

  position: relative;

  display: inline-block;

  margin: 0;
  margin-left: 10px;

  text-decoration: none;

  color: rgba(243, 242, 241, .88);
}

.header-nav li a img {
  position: relative;

  display: inline-block;

  width: 150px;
